  /*width: 66px;*/
  /*height: 34px;*/
  position: relative;
  overflow: hidden;
}

.u-toggle .toggleGroup[_v-56ee11b9] {
  position: absolute;
  width: 200%;
  top: 0;
  bottom: 0;
  left: 0;
  transition: left 0.35s;
  -webkit-transition: left 0.35s;
  -moz-user-select: none;
  -webkit-user-select: none;
}

.u-toggle.off .toggleGroup[_v-56ee11b9] {
  left: -100%;
}

.u-toggle .toggleOn[_v-56ee11b9], .u-toggle .toggleOff[_v-56ee11b9] {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 0;
  margin: 0;
  border: 0;
  border-radius: 0;
}

.u-toggle .toggleOn[_v-56ee11b9] {
  left: 0;
  right: 50%;
}

.u-toggle .toggleHandle[_v-56ee11b9] {
  position: relative;
  margin: 0 auto;
  padding-top: 0px;
  padding-bottom: 0px;
  height: 100%;
  width: 0px;
  border-width: 0 1px;
}

